.products-wrapper {
  padding-top: 38px;
  animation: slideUp 300ms linear;
  animation-delay: 150ms;
  @media (max-width: 991px) {
    padding-top: 38px;
  }
  @media (max-width: 480px) {
    padding-top: 32px;
  }
}
.products {
  display: flex;
  display: -webkit-flex;
  flex-wrap: wrap;
  -webkit-flex-wrap: wrap;
  padding: 16px;
  max-width: 960px;
  margin: 0 auto;
  @media (max-width: 768px) {
    padding: 8px;
  }
  .product {
    min-height: 355px;
    flex-basis: calc(25% - 32px);
    &.category{
      min-height: 250px;
    }
    -webkit-flex-basis: calc(25% - 32px);
    @media (max-width: 768px) {
      flex-basis: calc(33% - 16px);
      -webkit-flex-basis: calc(33% - 16px);
      margin: 8px;
    }
    @media (max-width: 480px) {
      flex-basis: calc(50% - 16px);
      -webkit-flex-basis: calc(50% - 16px);
      min-height: 313px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .product-name, .product-description {
      
      }
      .product-action {
        padding: 16px 8px;
      }
      &.category{
        min-height: 200px;
      }
     
      
    }
    @media (max-width: 320px) {
      flex-basis: 100%;
      -webkit-flex-basis: 100%;
    }
  }
}

.fadeIn-enter {
  opacity: 0.01;
}

.fadeIn-enter.fadeIn-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.fadeIn-leave {
  opacity: 1;
}

.fadeIn-leave.fadeIn-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

.product:nth-child(1) {
  transition-delay: 200ms;
}
.product:nth-child(2) {
  transition-delay: 200ms;
}
.product:nth-child(3) {
  transition-delay: 400ms;
}
.product:nth-child(4) {
  transition-delay: 600ms;
}
.product:nth-child(5) {
  transition-delay: 800ms;
}
.product:nth-child(6) {
  transition-delay: 1000ms;
}
.product:nth-child(7) {
  transition-delay: 1200ms;
}
.product:nth-child(8) {
  transition-delay: 1400ms;
}
.product:nth-child(9) {
  transition-delay: 1600ms;
}
.product:nth-child(10) {
  transition-delay: 1800ms;
}
.product:nth-child(11) {
  transition-delay: 2000ms;
}
.product:nth-child(12) {
  transition-delay: 2200ms;
}

// Loading
.product.loading {
  animation-duration: 600ms;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: contentLoader;
  animation-timing-function: linear;
  background: #f6f7f8;
  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
  background-size: 1000px 104px;
  height: 200px;
  position: relative;
  overflow: hidden;
  .product-image {
    height: 168px;
    width: 168px;
    margin: 16px;
    background: rgba(255, 255, 255, 0.25);
    @media (max-width: 768px) {
      width: auto;
    }
  }
  .product-text,
  .product-button {
    margin: 12px 16px;
    height: 16px;
    background: rgba(255, 255, 255, 0.25);
  }
  .product-button {
    position: absolute;
    bottom: 8px;
    width: 168px;
    height: 36px;
    @media (max-width: 768px) {
      width: auto;
    }
  }
}
@keyframes contentLoader {
  0% {
    background-position: -420px 0;
  }
  100% {
    background-position: 420px 0;
  }
}

.sub-category-icon{
  display: flex;
  flex-direction: column;
  margin: 10px;
  align-items: center;
  .sub-category-label{
    display: flex;
    width: 35px;
    height: 35px;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
  }
  .sub-category-text{
    font-size: 12px;
    max-width: 50px;
    text-align: center;
  }
}
.total-products{
  margin: 0 30px;
    font-style: italic;
    color: grey;
}
.total-products-wrapper{
  padding:0;
}
// Empty states
.no-results {
  text-align: center;
  max-width: 520px;
  margin: 0 auto;
  img {
    width: 320px;
    margin: 16px 0 32px 0;
    opacity: 0.33;
  }
  h2 {
    margin-bottom: 16px;
    color: #666;
  }
  p {
    color: #999;
  }
}
